import Vue from "vue";
import VueRouter from "vue-router";

import Home from "../views/HomePage.vue";
import login from "../views/LoginPage.vue";
import reslut from "../views/SearchResult.vue";
import signup from "../views/SignUp.vue";
import secl from "../components/SelectExp.vue";
import meeting from "../components/MeetingSelect.vue";
import info from "../components/DoctorInfo.vue";
Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "home",
    component: Home,
  },
  {
    path: "/signup",
    name: "signup",
    component: signup,
  },
  {
    path: "/info",
    name: "info",
    component: info,
  },
  {
    path: "/meeting",
    name: "meeting",
    component: meeting,
  },
  {
    path: "/result",
    name: "result",
    component: reslut,
  },
  {
    path: "/s",
    name: "s",
    component: secl,
  },

  {
    path: "/login",
    name: "login",
    component: login,
  },
  {
    path: "/reslut/:sp/:city/:area/:name",
    name: "reslut",
    component: reslut,
  },
  {
    path: "/about",
    name: "about",
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () =>
      import(/* webpackChunkName: "about" */ "../views/AboutView.vue"),
  },
];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  routes,
});

export default router;
