import Vue from "vue";
import Vuex from "vuex";
Vue.use(Vuex);
// const state = {
//   username: "",
// };
const store = new Vuex.Store({
  state: {
    username: null,
  },

  getters: {
    username: (state) => {
      return state.username;
    },
  },
  actions: {
    username(context, username) {
      context.commit("username", username);
    },
  },
  mutations: {
    username(state, username) {
      state.username = username;
    },
  },
});
export default store;
